.done__task {
  font-weight: 400;
  font-size: 16px;
  color: #9e78cf;
  border-radius: 10px;
  max-width: 432px;
  height: 75px;
  background: #15101c;
  padding: 22px 20px;
  line-height: 1.8;

  display: flex;
  justify-content: end;

  @media (max-width: 350px) {
    padding: 10px 15px;
    height: unset;
  }
}

.done__task span {
  display: block;
  margin-right: auto;
  text-decoration: line-through;
  color: #78cfb0;
}

.done__task img {
  cursor: pointer;

  @media (max-width: 350px) {
    object-fit: contain;
  }
}