.input__inner {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 15px;

  @media (max-width: 350px) {
    gap: 8px;
  }
}

.input__inner img {
  background: #9e78cf;
  border-radius: 10px;
  width: 40px;
  height: 40px;

  cursor: pointer;
}


.input {
  border: 1px solid #9e78cf;
  border-radius: 10px;
  width: 381px;
  height: 40px;
  background-color: #1D1825;

  padding: 10px 15px;
  color: white;

  font-family: 'Inter', sans-serif;
  font-weight: 400;
  font-size: 16px;
  color: #777;

  @media (max-width: 350px) {
    width: 230px;
  }
}